
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
#app {
  min-width: 1000px;
  position: absolute;
  height: 100%;
  width: 100%;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
a {
  color: #000;
}

.HelloBoyMain {
  height: 100%;
  width: 100%;
  overflow: auto;
}
.HelloBoyMain .HelloBoyMain-header {
  height: 80px;
}
.HelloBoyMain-logo {
  float: left;
  height: 100%;
  margin-left: 150px;
  width: 150px;
  background: url(./static/img/2018logo.8b443ba.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: top;
  cursor: pointer;
}
.HelloBoyMain-title {
  width: 100px;
  text-align: center;
  float: left;
  height: 80px;
  line-height: 80px;
  font-size: 15px;
  cursor: pointer;
}
.HelloBoyMain-login {
  float: right;
  height: 80px;
  line-height: 80px;
  margin-right: 80px;
}
.HelloBoyMain .router-link-active {
  font-weight: 700;
  color: #000;
}
.HelloBoyMain-img {
  margin: 15px 20px 15px 0px;
  height: 50px;
  width: 50px;
  background-size: contain;
  border: 1.5px solid greenyellow;
  float: right;
  border-radius: 50%;
  cursor: pointer;
}
.HelloBoyMain .nickname {
  height: 80px;
  margin: 0 15px;
  line-height: 80px;
  float: right;
  font-weight: 700;
  cursor: pointer;
  font-size: 15px;
}
.HelloBoyMain-lv {
  height: 80px;
  line-height: 80px;
  float: right;
  font-weight: 700;
  cursor: pointer;
  font-size: 19px;
  text-align: left;
  color: gold;
}

.HelloBoyHome {
  height: 650px;
  width: 100%;
}
.HelloBoyHome .carousel1 {
  height: 650px;
  background: url(./static/img/2018.c371ed6.jpg);
  background-size: 100% 100%;
}
.HelloBoyHome .carousel2 {
  height: 650px;
  background: url(./static/img/helloboyhome1.5b74eef.jpg);
  background-size: cover;
}
.HelloBoyHome .carousel3 {
  height: 650px;
  background: url(./static/img/Scenery2.5e4b7b1.jpg);
  background-size: 100% 100%;
}
.HelloBoyHome-title {
  height: 100px;
  line-height: 150px;
  font-size: 25px;
  font-weight: 800;
}
.HelloBoyHome-title-e {
  height: 50px;
  font-size: 13px;
  font-weight: 600;
}
.time {
  font-size: 14px;
  font-weight: bold;
}
.content {
  padding-left: 5px;
}
.HelloBoyHome-Timeline {
  color: #c57956;
  margin-top: 30px;
  position: relative;
  padding: 70px 30%;
  background: url(./static/img/shutterstock.eeed8e1.jpg);
  background-size: cover;
}
.HelloBoyHome-footer {
  height: 300px;
  background: rgba(0, 0, 0, 0.842);
}
.HelloBoyHome-add {
  height: 250px;
  line-height: 250px;
  font-size: 25px;
  font-weight: 800;
}
.HelloBoyHome-footer-text {
  color: #fff;
  font-size: 25px;
  font-weight: 800;
  height: 250px;
  line-height: 250px;
}

.Login {
  height: 100%;
  width: 100%;
  background: url(./static/img/login.951b656.jpg);
  color: white;
}
.Login-title {
  font-size: 45px;
  margin-top: 300px;
}
.Login-l {
  width: calc(100% - 600px);
  height: 100%;
  float: left;
}
.Login-r {
  width: 600px;
  height: 100%;
  float: left;
}
.login-main {
  position: relative;
  margin: 150px 0 0 50px;
  width: 350px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
}
.login-l {
  width: 50%;
}
.login-content {
  padding: 50px 30px;
}
.Login .ivu-tabs-nav {
  width: 100%;
}
.Login .ivu-tabs-nav-container {
  font-size: 18px;
}
.Login .ivu-tabs {
  color: white;
}
.Login .ivu-tabs-nav .ivu-tabs-tab {
  width: 50%;
  padding: 15px 16px;
  color: white;
}

.ErrorPage {
  height: 100%;
  width: 100%;
  background-image: url(./static/img/error.0ce3c75.jpg);
  background-size: 100% 100%;
}

.AboutOurs {
  height: 100%;
  width: 100%;
  overflow: auto;
}

.ConnectionAuthor {
  height: 100%;
  width: 100%;
  overflow: auto;
}
